video{
    object-fit: cover;
    width: 100vw;
    height: 100vh;
    position: fixed;
    z-index: -1;
    
}
.hero-container{
    /* background-image: url('..public/images/img-home.jpg') center center/cover no-repeat; */
    height: 100vh;
    width:100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2);
    object-fit: contain;
}
.hero-container>h1{
    color: white;
    font-size: 100px;
    margin-top:100px;
}
.hero-container>p{
    margin-top: 8px;
    color: white;
    font-size: 32px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;

}
.hero-btns{
    margin-top: 32px;
}
.hero-btns .btns{
    margin: 6px;
}
.fa-play-circle{
    margin-left: 4px;
}

@media screen and (max-width: 960px){
    .hero-container >h1{
        font-size: 70px;
    }
    .hero-container >p{
        font-size: 35px;
    }
    /* video{
       width: 960px; 
    } */
}
@media screen and (max-width:768px) {
    .hero-container>p{
        font-size: 20px;
    }
    .hero-container >h1{
        font-size: 40px;
    }
    .btn-mobile{
        display: block;
        text-decoration: none;
    }
    .btn{
        width: 100%;
    }
    /* video{
        width: px; 
     } */
    
}